import React from 'react';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import styles from './NavigationWideScreens.module.css';

const navList = [
    {
        title: 'About me',
        link: 'about'
    },
    { title: 'Relationships', link: '' },
    { title: 'Requirements', link: '' },
    {
        title: 'Users',
        link: "users"
    },
    {
        title: 'Sign Up',
        link: "CTA"
    },
];

const NavigationWideScreens = () => {
    const navItems = navList.map((el, i) => (
        <Link to={el.link || "CTA"} spy={true} smooth={true} duration={250} key={i} className={styles.navItem} href="#">
            {el.title}
        </Link>
    ));
    return (
        <nav>
            {navItems}
        </nav>
    )
}

export default NavigationWideScreens
